export default {
  url: "reports/lists/sanads",
  cols: [{
      th: "",
      td: "code",
      type: "number",
      filters: ["code"]
    },
    {
      th: "",
      td: "bed",
      type: "money",
      filters: ["bed__icontains"]
    },
    {
      th: "",
      td: "bes",
      type: "money",
      filters: ["bes__icontains"]
    },
    {
      th: "",
      td: "date",
      type: "date",
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "date__lte"
        }
      ]
    },
    {
      th: " ",
      td: "type",
      type: "select",
      options: [{
          label: '',
          value: 'temporary'
        },
        {
          label: '',
          value: 'definite'
        },
      ],
      filters: ["type"]
    },
    {
      th: "",
      td: "explanation",
      type: "text",
      filters: ["explanation__icontains"]
    },
  ],
  label: '',
  routerName: 'SanadForm'
}
