export default {
  url: "reports/journal",
  cols: [{
      th: "",
      td: "sanad.code",
      type: "number",
      filters: ["code__icontains"]
    },
    {
      th: "",
      td: "sanad.date",
      type: "date",
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "date__lte"
        }
      ]

    },
    {
      th: " ",
      td: "account.code",
      type: "number",
      filters: [{
        label: '  ',
        model: "account__code__gte"
      }, {
        label: '  ',
        model: "account__code__lte"
      }]
    },
    {
      th: " ",
      td: "account.name",
      type: "text",
      filters: ["account__name__icontains"]
    },
    {
      th: "",
      td: "explanation",
      type: "text",
      filters: ["explanation__icontains"]
    },
    {
      th: "",
      td: "bed",
      type: "money",
    },
    {
      th: "",
      td: "bes",
      type: "money",
    },
  ],
}
