<template>
  <m-form
    title=" "
    :items="items"
    :cols="cols"
    :canSubmit="canSubmit"
    :canDelete="canDelete"
    :is-editing.sync="isEditing"
    :showListBtn="false"
    :show-navigation-btns="false"
    @rowClick="setItem"
    @clearForm="clearForm"
    @submit="submit"
    @delete="deleteItem"
  >
    <template #default>
      <v-row>
        <v-col cols="12" md="12">
          <v-text-field label=" * " v-model="item.name" :disabled="!isEditing" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field
            label=" "
            v-model="item.shenasname_number"
            :disabled="!isEditing"
          />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.melli_code" :disabled="!isEditing" />
        </v-col>
        <v-col cols="12" md="4">
          <date label=" " v-model="item.date_of_birth" :disabled="!isEditing" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.father_name" :disabled="!isEditing" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.phone" :disabled="!isEditing" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.landline_phone" :disabled="!isEditing" />
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field
            label=" "
            v-model="item.driving_licence_number"
            :disabled="!isEditing"
          />
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field
            label="  "
            v-model="item.health_card_number"
            :disabled="!isEditing"
          />
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field
            label="  "
            v-model="item.bank_card_number"
            :disabled="!isEditing"
          />
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field
            label="  "
            v-model="item.bank_account_number"
            :disabled="!isEditing"
          />
        </v-col>
        <v-col cols="12" md="8">
          <v-text-field label=" " v-model="item.iban" :disabled="!isEditing" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.bank_name" :disabled="!isEditing" />
        </v-col>
        <v-col cols="12">
          <v-textarea label="" v-model="item.explanation" :disabled="!isEditing" />
        </v-col>
      </v-row>
    </template>
  </m-form>
</template>
<script>
import ListModalFormMixin from "@/components/mcomponents/form/ListModalForm.js";
import GetApi from "./GetApi";

export default {
  mixins: [ListModalFormMixin, GetApi],
  data() {
    return {
      item: {},
      baseUrl: "dashtbashi/drivers",
      permissionBasename: "driver",
      leadingSlash: true,
      cols: [
        {
          th: "",
          td: "name",
          type: "text",
          filters: ["name"]
        }
      ]
    };
  },
  computed: {
    items() {
      return this.$store.state.drivers;
    }
  },
  methods: {
    getData() {
      this.getDrivers(true);
    },
    getSerialized() {
      let item = this.extractIds(this.item);
      return item;
    }
  }
};
</script>