export default {
  url: "reports/lists/receipts",
  routerName: 'ReceiptAndRemittance',
  cols: [{
      th: "",
      td: "code",
      type: "number",
      filters: ["code__icontains"]
    },
    {
      th: "",
      td: "date",
      type: "date",
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "date__lte"
        }
      ]
    },
    {
      th: "",
      td: "time",
      type: "time",
      filters: [{
          label: " ",
          model: "time__gte"
        },
        {
          label: " ",
          model: "time__lte"
        }
      ]
    },
    {
      th: "",
      td: "explanation",
      type: "text",
      filters: ["explanation__icontains"]
    },
  ],
}
