export default {
  url: "reports/lists/factors",
  routerName: 'FactorForm',
  cols: [{
      th: "",
      td: "code",
      type: "number",
      filters: ["code__icontains"]
    },
    {
      th: "",
      td: "date",
      type: "date",
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "date__lte"
        }
      ]
    },
    {
      th: "",
      td: "time",
      type: "time",
      filters: [{
          label: " ",
          model: "time__gte"
        },
        {
          label: " ",
          model: "time__lte"
        }
      ]
    },
    {
      th: "",
      td: "account.name",
      type: "text",
      filters: ["account__name__icontains"]
    },
    {
      th: "",
      td: "explanation",
      type: "text",
      filters: ["explanation__icontains"]
    },
    {
      th: "  ",
      td: "total_sum",
      type: "money",
      filters: [],
      sortable: false,
      
    },
  ],
}
