export default {
  url: "reports/ledger",
  cols: [{
      th: "",
      td: "sanad.date",
      type: "date",
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "date__lte"
        }
      ]
    },
    {
      th: " ",
      td: "sanad.code",
      type: "number",
      filters: ["code__icontains"]
    },
    {
      th: "",
      td: "explanation",
      type: "text",
      filters: ["explanation__icontains"]
    },
    {
      th: " ",
      td: "account.code",
      type: "number",
      filters: [{
        label: '  ',
        model: "account__code__gte"
      }, {
        label: '  ',
        model: "account__code__lte"
      }]
    },
    {
      th: " ",
      td: "account.name",
      type: "text",
      filters: ["account__name__icontains"]
    },
    {
      th: "",
      td: "bed",
      type: "money",
      sortable: false,
    },
    {
      th: "",
      td: "bes",
      type: "money",
      sortable: false,
    },
    {
      th: "",
      td: "remain",
      type: "money",
      sortable: false,
      render(item, items, oldItems) {
        return item.bed - item.bes;
      },
    },
    {
      th: "",
      td: "remainType",
      type: "string",
      sortable: false,
      render(item) {
        let diff = item.bed > item.bes;
        if (diff == 0) return '';
        if (diff > 0) return '';
        if (diff < 0) return '';
        return 'error';
      }
    },
  ],
}
