export default {
  url: "reports/buySale",
  cols: [{
      th: " ",
      td: "factor.type",
      type: "select",
      options: [
        {
          label: '  ',
          value: 'fpi',
        },
        {
          label: '',
          value: 'sale',
        },
        {
          label: '  ',
          value: 'backFromSale',
        },
        {
          label: '',
          value: 'buy',
        },
        {
          label: '  ',
          value: 'backFromBuy',
        }
      ],
      filters: ["factor__type"],
    },
    {
      th: "",
      td: "factor.is_definite",
      type: "select",
      options: [
        {
          label: '',
          value: true,
        },
        {
          label: '',
          value: false,
        },
      ],
      filters: ["factor__is_definite"],
    },
    {
      th: "",
      td: "factor.date",
      type: "date",
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "factor__date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "factor__date__lte"
        }
      ]
    },
    {
      th: " ",
      td: "factor.id",
      type: "number",
      filters: ["factor__id"]
    },
    {
      th: " ",
      td: "factor.code",
      type: "number",
      filters: ["factor__code"]
    },
    {
      th: "/",
      td: "factor.account.name",
      type: "text",
      filters: ["factor__account__name__icontains"]
    },
    {
      th: "",
      td: "warehouse.name",
      type: "text",
      filters: ["warehouse__name__icontains"]
    },
    {
      th: "",
      td: "count",
      type: "money",
      filters: ['count'],
      sortable: false,
    },
    {
      th: "",
      td: "fee",
      type: "money",
      filters: ['fee'],
      sortable: false,
    },
    {
      th: "",
      td: "value",
      type: "money",
      filters: [],
      sortable: false,
    },
    {
      th: "",
      td: "discount",
      type: "money",
      filters: [],
      sortable: false,
    },
    {
      th: " ",
      td: "total_value",
      type: "money",
      filters: [],
      sortable: false,
    },
    {
      th: " ",
      td: "factor__explanation",
      type: "text",
      filters: ["factor__explanation__icontains"]
    },
    {
      th: "",
      td: "explanation",
      type: "text",
      filters: ["explanation__icontains"]
    },
  ],
}
