export default {
  url: "reports/lists/transactions",
  cols: [{
      th: "",
      td: "code",
      type: "number",
      filters: ["code__icontains"]
    },
    {
      th: " ",
      td: "account.name",
      type: "text",
      filters: ["account__name__icontains"]
    },
    {
      th: "",
      td: "date",
      type: "date",
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "date__lte"
        }
      ]
    },
    {
      th: "",
      td: "explanation",
      type: "text",
      filters: ["account__icontains"]
    },
    {
      th: " ",
      td: "sanad.bed",
      type: "money",
      filters: ["sanad__bed__icontains"]
    }
  ],
  routerName: 'TransactionForm',
}
