export default {
  url: "reports/lists/cheques",
  routerName: 'ChequeDetail',
  cols: [{
      th: "",
      td: "serial",
      type: "text",
      filters: ["serial__icontains"]
    },
    {
      th: "",
      td: "explanation",
      type: "text",
      filters: ["account__icontains"]
    },
    {
      th: "",
      td: "date",
      type: "date",
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "date__lte"
        }
      ]
    },
    {
      th: " ",
      td: "due",
      type: "date",
      filters: [{
          label: " ",
          model: "due__gte"
        },
        {
          label: " ",
          model: "due__lte"
        }
      ]
    },
    {
      th: "",
      td: "value",
      type: "money",
      filters: ["value"]
    },
    {
      th: " ",
      td: "account.name",
      type: "text",
      filters: ["account__name__icontains"]
    },
    {
      th: "",
      td: "status",
      type: "select",
      options: [{
          label: '',
          value: 'blank'
        },
        {
          label: ' ',
          value: 'notPassed'
        },
        {
          label: ' ',
          value: 'inFlow'
        },
        {
          label: ' ',
          value: 'passed'
        },
        {
          label: '',
          value: 'bounced'
        },
        {
          label: '',
          value: 'cashed'
        },
        {
          label: ' ',
          value: 'revoked'
        },
        {
          label: '',
          value: 'transferred',
        }
      ],
      filters: ["status"]
    },
    {
      th: " ",
      td: "bankName",
      type: "text",
      filters: ["bankName__icontains"]
    }
  ],
}